<template>
	<div class="jp-authorityResource">
		<!---->
		<div class="menu-con">
			<p class="title"></p>
			<div class="menu-list">
				<div class="item">
					<div class="item-con"></div>
					<div class="item-ipt">
						<el-input v-model.trim="name" placeholder="" @keyup.enter.native="onSearch($event)"></el-input>
					</div>
				</div>
				<div class="item">
					<div class="item-con"></div>
					<div class="item-ipt">
						<el-select v-model="permissionID" filterable placeholder="">
							<el-option v-for="(val,index) in permissionList" :key="val.id" :label="val.name" :value="val.id"></el-option>
						</el-select>
					</div>
				</div>
				<div class="item">
					<button class="search" @click="onSearch"></button>
					<button class="add" @click="dialogAdd = true"></button>
				</div>
			</div>
		</div>
		<!---->
		<div class="data" v-loading="loading" element-loading-text="">
			<table>
				<thead>
					<tr>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(item,index) in list" :key="index">
						<td>{{(curPage-1)*10 + index + 1}}</td>
						<td v-html="item.name"></td>
						<td v-html="item.permissionPackage.name"></td>
						<td>{{item.status == 1 ? '' : ''}}</td>
						<td>
							<el-button class="edit-btn" type="text" @click="edit(item.id)"></el-button>
							<el-button class="del-btn" type="text" @click="del(item.id,index)"></el-button>
						</td>
					</tr>
					<no-data v-if="flag"></no-data>
				</tbody>
			</table>
			<div id="page">
				<rt-page ref="page" :cur="curPage" :total="total" @change="pageHandler"></rt-page>
			</div>
		</div>
		<!---->
		<el-dialog class="xinjian" title="" :visible.sync="dialogAdd">
			<el-form v-loading="loadingNew" :model="newBuild" :rules="rulesAdd" ref="newBuild">
				<el-form-item label="" :label-width="formLabelWidth" placeholder="" prop="name">
					<el-input v-model.trim="newBuild.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="packageId">
					<el-select v-model="newBuild.packageId" placeholder="">
						<el-option v-for="(val,index) in permissionList" :key="val.id" :label="val.name" :value="val.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="URL" :label-width="formLabelWidth" prop="url">
					<el-input v-model.trim="newBuild.url" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio class="radio" v-model="newBuild.radio" label="1"></el-radio>
					<el-radio class="radio" v-model="newBuild.radio" label="0"></el-radio>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="memo">
					<el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="" v-model.trim="newBuild.memo">
					</el-input>
				</el-form-item>
				<el-form-item class="btn">
					<el-button class="save" type="primary" @click="addSAVE('newBuild')"></el-button>
					<el-button class="off" @click="addOFF('newBuild')"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
		<!---->
		<el-dialog class="xinjian" title="" :visible.sync="dialogEdit">
			<el-form v-loading="loadingEdit" :model="editDialog" :rules="rulesEdit" ref="editDialog">
				<el-form-item label="" :label-width="formLabelWidth" placeholder="" prop="name">
					<el-input v-model.trim="editDialog.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="packageId">
					<el-select v-model="editDialog.packageId" :placeholder="editDialog.idName">
						<el-option v-for="(val,index) in permissionList" :key="val.id" :label="val.name" :value="val.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="URL" :label-width="formLabelWidth" prop="url">
					<el-input v-model.trim="editDialog.url" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio class="editradio" v-model="editDialog.radio" label="1"></el-radio>
					<el-radio class="editradio" v-model="editDialog.radio" label="0"></el-radio>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth" prop="memo">
					<el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="" v-model.trim="editDialog.memo">
					</el-input>
				</el-form-item>
				<el-form-item class="btn">
					<el-button class="save" type="primary" @click="editSAVE('editDialog')"></el-button>
					<el-button class="off" @click="editOFF('editDialog')"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
	</div>
</template>
<script>
	import rtPage from '@/components/pagination'
	import noData from '@/components/noData'
	import md5 from 'js-md5'
	export default {
		data() {
			//
			var patternNameNew = (rule, value, callback) => {
				var aa = /[@#\$%\^&\*<>!(),\.]+/g;
				if(value == "") {
					callback("")
				} else if(aa.test(value) == true) {
					callback("")
				} else {
					this.axios.post("/permission/selectPermissionByNameNoPage", {
							name: value
						})
						.then((res) => {
							if(res.data.data) {
								callback()
							} else {
								callback("")
							}
						}).catch((error) => {
							console.log(error);
						})
				}
			};
			var patternNameEdit = (rule, value, callback) => {
				var aa = /[@#\$%\^&\*<>!(),\.]+/g;
				if(value == "") {
					callback("")
				} else if(aa.test(value) == true) {
					callback("")
				} else {
					if(value == this.editName) {
						callback()
					} else {
						this.axios.post("/permission/selectPermissionByNameNoPage", {
								name: value
							})
							.then((res) => {
								if(res.data.data) {
									callback()
								} else {
									callback("")
								}
							}).catch((error) => {
								console.log(error);
							})
					}
				}
			};
			return {
				//,
				loadingNew: false,
				loadingEdit: false,
				//
				flag: false,
				//
				loading: false,
				/*id*/
				name: '',
				permissionID: '',
				searchStatus: 0,
				/**/
				permissionList: '',
				/**/
				total: 0,
				curPage: 1,
				list: "",
				/*id*/
				userID: '',
				/**/
				dialogAdd: false,
				dialogEdit: false,
				/**/
				newBuild: {
					name: '',
					packageId: null,
					radio: "1",
					memo: '',
					url: ''
				},
				/**/
				editDialog: {
					idName: "",
					name: '',
					packageId: null,
					radio: "1",
					memo: '',
					url: ''
				},
				formLabelWidth: '80px',
				editName: '',
				/**/
				rulesAdd: {
					name: [{
						validator: patternNameNew,
						trigger: 'blur'
					}, {
						max: 8,
						message: '  8',
						trigger: 'blur'
					}],
					packageId: [{
						required: true,
						message: ''
					}],
					url: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					memo: [{
						max: 30,
						message: '  30',
						trigger: 'blur'
					}]
				},
				/**/
				rulesEdit: {
					name: [{
						validator: patternNameEdit,
						trigger: 'blur'
					}, {
						max: 8,
						message: '  8',
						trigger: 'blur'
					}],
					packageId: [{
						required: true,
						message: ''
					}],
					url: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					memo: [{
						max: 30,
						message: '  30',
						trigger: 'blur'
					}]
				}
			}
		},
		components: {
			rtPage,
			noData
		},
		mounted() {
			this.permissionPackage();
			this.restaurants = this.permissionPackage();
		},
		methods: {
			//
			ajaxData(data) {
				this.axios.post("/permission/selectPackagePermissionByConditon", data)
					.then((res) => {
						this.loading = false;
						if(res.data.list.length == 0) {
							this.flag = true;
							this.list = '';
							this.total = 0;
						} else {
							this.flag = false
							this.list = res.data.list;
							this.total = Number(res.data.total);
						}
					}).catch((error) => {
						console.log(error);
						this.flag = true;
						this.loading = false;
					})
			},
			//
			pageHandler(curPage) {
				//
				this.loading = true
				this.curPage = curPage;
				let data = {
					name: this.searchStatus == 0 ? '' : this.name, //
					packageId: this.searchStatus == 0 ? '' : this.permissionID, //id
					pageIndex: this.curPage, //
					size: 10 //
				};
				this.ajaxData(data)
			},
			//
			onSearch(ev) {
				var aa = /[@#\$%\^&\*<>!(),\.]+/g;
				if(aa.test(this.name) == true) {
					alert("")
				} else {
					//
					this.loading = true;
					//
					this.total = 0;
					this.searchStatus = 1;
					let data = {
						name: this.searchStatus == 0 ? '' : this.name, //
						packageId: this.searchStatus == 0 ? '' : this.permissionID, //id
						pageIndex: this.searchStatus == 0 ? '' : this.curPage, //
						size: 10 //
					};
					this.ajaxData(data)
				}
			},
			//
			permissionPackage() {
				this.axios.post("/permissionPackage/selectAllPackage", {
					status: 2 //2
				}).then((res) => {
					this.permissionList = res.data.data;
				}).catch((error) => {
					console.log(error);
				})
			},
			//
			addSAVE(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.loadingNew = true;
						let data = {
							name: this.newBuild.name, //
							packageId: this.newBuild.packageId, //id
							resource: this.newBuild.url, //
							memo: this.newBuild.memo, //
							status: this.newBuild.radio //
						};
						this.axios.post("/permission/savePermission", data)
							.then((res) => {
								this.loadingNew = false;
								if(res.data) {
									this.dialogAdd = false;
									this.name = "";
									this.permissionID = "";
									//
									this.total = 0;
									this.pageHandler(1);
									this.$refs[formName].resetFields();
									this.$message({
										message: "",
										type: "success"
									});
								} else {
									this.$message.error("");
								}
							}).catch((error) => {
								console.log(error);
								this.loadingNew = false;
							});
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			//
			addOFF(formName) {
				this.dialogAdd = false;
				//
				this.$refs[formName].resetFields();
			},
			//
			edit(id) {
				this.userID = id;
				this.dialogEdit = true;
				let data = {
					id: id
				};
				this.axios.post("/permission/selectPermissionById", data)
					.then((res) => {
						this.userID = res.data.id; //id
						this.editDialog.name = res.data.name; //
						this.editName = res.data.name;
						this.editDialog.idName = res.data.permissionPackage.name; //
						this.editDialog.packageId = res.data.permissionPackage.id; //id
						this.editDialog.radio = "" + res.data.status + ""; //
						this.editDialog.url = res.data.resource; //
						this.editDialog.memo = res.data.memo; //
					}).catch((error) => {
						console.log(error);
					});
			},
			//
			editSAVE(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.loadingEdit = true;
						let data = {
							id: this.userID, //id
							packageId: this.editDialog.packageId, //id
							name: this.editDialog.name, //
							resource: this.editDialog.url, //
							memo: this.editDialog.memo, //
							status: this.editDialog.radio //
						};
						this.axios.post("/permission/updatePermissionById", data)
							.then((res) => {
								this.loadingEdit = false;
								if(res.data) {
									this.pageHandler(this.curPage)
									this.dialogEdit = false;
									this.$refs[formName].resetFields();
									this.$message({
										message: "",
										type: "success"
									});
								} else {
									this.$message.error("");
								}
							}).catch((error) => {
								console.log(error);
								this.loadingEdit = false;
							});
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			//
			editOFF(formName) {
				this.dialogEdit = false;
				this.$refs[formName].resetFields();
			},
			//
			del(id, index) {
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					let data = {
						id: id
					};
					this.axios.post("/permission/delPermissionById", data)
						.then((res) => {
							if(res.data) {
								this.pageHandler(this.curPage)
								this.$message({
									type: 'success',
									message: '!'
								});
							} else {
								this.$message.error("");
							}
						}).catch((error) => {
							console.log(error);
						});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
		},
		watch: {
			name: function() {
				this.searchStatus = 0
			}
		}
	}
</script>
<style lang="less" scoped="scoped">
	.jp-authorityResource {
		.menu-con {
			background: #fff;
			padding: 0 20px;
			box-shadow: 0 0 10px #9E9EA1;
			.title {
				font-size: 14px;
				font-weight: 400;
				color: #787878;
				padding: 14px 0 14px 14px;
				border-bottom: 1px solid #cccccc;
			}
			.menu-list {
				height: 88px;
				padding: 30px 0;
				line-height: 28px;
				position: relative;
				.item {
					float: left;
					.item-con {
						margin: 0 14px;
						text-align: center;
						float: left;
						font-size: 14px;
						color: #333
					}
					.item-ipt {
						height: 28px;
						float: left;
						position: relative;
						.el-input {
							width: 140/32rem;
							min-width: 140px;
						}
					}
					button {
						width: 58/32rem;
						min-width: 60px;
						height: 38px;
						border-radius: 4px;
						border: none;
						color: #fff;
						margin-top: -2px;
						cursor: pointer;
					}
					.search {
						background: #0190D4;
						position: absolute;
						top: 28px;
						left: 75%;
					}
					.add {
						background: #f33535;
						position: absolute;
						top: 28px;
						left: 88%;
					}
				}
			}
		}
		.data {
			height: 642px;
			background: #ffffff;
			margin-top: 30px;
			position: relative;
			table {
				width: 100%;
				font-size: 12px;
				thead {
					background: #abc2d7;
					color: #ffffff;
					height: 40px;
					line-height: 40px;
					td:nth-child(1) {
						width: 62/32rem;
					}
					td:nth-child(2),
					td:nth-child(3),
					td:nth-child(4),
					td:nth-child(5) {
						width: 176/32rem;
					}
				}
				tbody {
					tr {
						height: 50px;
						line-height: 30px;
						color: #666666;
						td {
							height: 50px;
							line-height: 50px;
							border-bottom: 1px solid #cccccc;
						}
					}
					tr:hover {
						background: #EEF1F6;
					}
				}
				td {
					text-align: center;
					border-right: 1px solid #cfd0d0;
				}
				td:last-child {
					border-right: none;
				}
			}
			#page {
				width: 100%;
				position: absolute;
				bottom: 19px;
				text-align: center;
			}
		}
		.xinjian {
			p {
				color: red;
				position: absolute;
				height: 20px;
				line-height: 20px;
			}
		}
	}
</style>


